import React , {useState} from 'react';
import{Link} from "react-router-dom";
import "./Navbar.css";
// eslint-disable-next-line
const Navbar = () => {
    const [isMobile,setIsMobile]  = useState(false);
  return (
    <nav className='navbar'>
  
    <h3 className='logo'><img src={require('../../images/logo.png')}  className="logocolor"/></h3>
    <ul className={isMobile ? 'nav-links-mobile' : 'nav-links'}
    onClick={()=>setIsMobile(false)}>

        <Link to ="/" className='home'>
            <li>Home</li>
        </Link>
        <Link to ="/service" className='about'>
            <li>Services</li>
        </Link>
        <Link to ="/recommend" className='skills'>
            <li>Jobs</li>
        </Link>
        <Link to ="/testimonials" className='contact'>
            <li>Testimonials</li>
        </Link>
    </ul>
<button className='mobile-menu-icon' onClick={
    ()=>setIsMobile(!isMobile)
    }>
    {
        isMobile ? (<i className ="fas fa-times"></i>) : 
    (<i className="fas fa-bars"></i>)
    }
</button>
<button>Connect</button>
    </nav>
    
    );

};


export default Navbar;