import React from "react";
import "./Project.css";

function Project() {
  return (
    <section className="jh-section jh-site-stats-section v1" >
<div className="container">
<div className="jh-site-stats-inner">
<div className="section-header">
<h3 className="section-title">Project Site Stats</h3><span className="section-sub-title">Here we list our site stats and how many people weve helped find a job and companies have found recruits. It's a pretty awesome stats area!</span> </div>
<ul className="site-stats-list">
<li className="site-stats">
<span className="stats-count">18</span><h4 className="stats-title">Jobs Posted</h4> </li>
<li className="site-stats">
<span className="stats-count">38</span><h4 className="stats-title">Jobs Filled</h4> </li>
<li className="site-stats">
<span className="stats-count">67</span><h4 className="stats-title">Companies</h4> </li>
<li className="site-stats">
<span className="stats-count">92</span><h4 className="stats-title">Members</h4> </li>
</ul>
</div>
</div>
</section>
  );
}

export default Project;